.Header {
    margin-top: 40px;
    margin-bottom: 40px;
}
header {
    display: flex;
    justify-content: space-between;
    margin: auto;
}

header img {
    max-width: 100%;
    max-width: 200px;
    height: auto;
  
  }

  @media screen and (max-width: 650px) {
    header img {
      max-width: 150px; 
      
    }
  }